import { AbiCoder, Interface } from '@ethersproject/abi';
import { parseEther, parseUnits } from '@ethersproject/units';
import { TransactionType } from './types';
var erc20Interface = new Interface(['function transfer(address recipient, uint256 amount) public returns (bool)']);

var encodeErc20Transfer = function encodeErc20Transfer(tx) {
  return erc20Interface.encodeFunctionData('transfer(address,uint256)', [tx.to, parseUnits(tx.amount, tx.decimals)]);
};

var erc721Interface = new Interface(['function safeTransferFrom(address _from, address _to, uint256 _tokenId) external payable']);

var encodeErc721Transfer = function encodeErc721Transfer(tx) {
  return erc721Interface.encodeFunctionData('safeTransferFrom(address,address,uint256)', [tx.from, tx.to, tx.tokenId]);
};

var abiCoder = new AbiCoder();

var defaultValue = function defaultValue(paramType) {
  return abiCoder._getCoder(paramType).defaultValue();
};

var encodeFunctionCall = function encodeFunctionCall(tx) {
  var iface = new Interface(tx.abi);
  var values = iface.functions[tx.functionSignature].inputs.map(function (input) {
    return tx.inputValues[input.name] || defaultValue(input);
  });
  return iface.encodeFunctionData(tx.functionSignature, values);
};

export var encodeSingle = function encodeSingle(tx) {
  switch (tx.type) {
    case TransactionType.transferFunds:
      if (!tx.token) {
        // transfer ETH
        return {
          to: tx.to,
          value: parseEther(tx.amount).toString(),
          data: '0x'
        };
      } else {
        // transfer ERC20 token
        return {
          to: tx.token,
          value: '0',
          data: encodeErc20Transfer(tx)
        };
      }

    case TransactionType.transferCollectible:
      return {
        to: tx.address,
        value: '0',
        data: encodeErc721Transfer(tx)
      };

    case TransactionType.callContract:
      return {
        to: tx.to,
        value: tx.value || '0',
        data: encodeFunctionCall(tx)
      };

    case TransactionType.raw:
      return {
        to: tx.to,
        value: tx.value || '0',
        data: tx.data || '0x'
      };
  }
};